<!-- eslint-disable -->
<template>
  <div class="confianca-wrapper">
    <div class="confianca">
      <div class="top movement">
        <div class="subtext">AS PESSOAS CONFIAM EM NÓS</div>
        <div class="title main">{{title}}</div>
        <div class="line"></div>
        <div class="copy" v-html="text">
          <!-- Lorem ipsum dolor sit amet, consectetur adipisifwcing elit, sed do eiusmod tempor incididunt ut
          labore et dolore roipi magna aliqua. Ut enim ad minim veeniam, quis nostruklad exercitation ullamco
          laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in tufpoy-->
        </div>
      </div>
      <div class="topics">
        <div class="topic movement" v-for="t in topics" :key="t.id">
          <div class="icon" :style="{ 'background-image': 'url(' + t.icon + ')' }"></div>
          <div class="title">{{ t.title }}</div>
          <div class="txt" v-html="t.text"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";

export default {
  data() {
    return {
      title: "",
      text: "",
      topics: [
        {
          id: 0,
          title: "",
          text: "",
          icon: "",
        },
      ],
    };
  },
  methods: {
    FadeIn() {
      var allMods = $(".movement, .cover");
      var delayedMods = $(".movement-delayed");
      var lateralMods = $(".lateral, .lateralR");
      var svg = $("#svg");

      (function ($) {
        $.fn.visible = function (partial, adjust) {
          if (adjust === undefined) {
            adjust = 0;
          }
          var elem = $(this),
            wind = $(window),
            viewTop = wind.scrollTop(),
            viewBottom = viewTop + wind.height(),
            elem_top = elem.offset().top + adjust,
            elem_bottom = elem_top + elem.height() - adjust,
            compareTop = partial === true ? elem_bottom : elem_top,
            compareBottom = partial === true ? elem_top : elem_bottom;

          return compareBottom <= viewBottom && compareTop >= viewTop;
        };
      })(jQuery);

      var win = $(window);

      var xmlTeste = true;

      allMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          el.addClass("placed");
        }
      });
      svg.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          xmlTeste &&
            el.append(
              "<img src='assets/img/elemento_animated.svg' alt=''></img>"
            );
          xmlTeste = false;
        }
      });
      lateralMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 100)) {
          el.addClass("placed");
        }
      });

      delayedMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 800)) {
          el.addClass("placed");
        }
      });

      win.scroll(function (event) {
        allMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            el.addClass("placed");
          }
        });
        lateralMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 100)) {
            el.addClass("placed");
          }
        });
        delayedMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 800)) {
            el.addClass("placed");
          }
        });
        svg.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            xmlTeste &&
              el.append(
                "<img src='assets/img/elemento_animated.svg' alt=''></img>"
              );
            xmlTeste = false;
          }
        });
      });
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://www.anacarolinapereira.pt/db-api/getAbout.php")
      .then((resp) => {
        const data = resp.data;
        var result = data.split("||");
        var intro = JSON.parse(result[4]);
        var confianca = JSON.parse(result[5]);
        var confiancaArray = [];

        vm.title = intro[0].title;
        vm.text = intro[0].text;

        map.call(confianca, function (c) {
          var src = c.img_src.split("uploads");
          confiancaArray.push({
            id: c.id,
            title: c.title,
            text: c.text,
            icon: "uploads" + src[1],
          });
        });

        vm.$data.topics = confiancaArray;
      })
      .then(() => {
        vm.FadeIn();
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.confianca-wrapper {
  background-color: $white;
  height: pxToEm(790);
  padding: $pd-desktop;
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media only screen and (min-width: 1200px) and (max-width: 1919px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
    height: auto;
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  .confianca {
    margin: auto;
    max-width: pxToEm(1400);
    width: 100%;
    display: flex;
    flex-direction: column;

    .top {
      display: flex;
      flex-direction: column;
      align-items: center;

      @media only screen and (max-width: 991px) {
        margin-top: pxToEm(40);
      }

      .subtext {
        text-transform: uppercase;
        color: #828282;
        font-size: pxToEm(16);
      }

      .title {
        color: #828282;
        font-size: pxToEm(30);
        margin-top: pxToEm(30);

        @media only screen and (max-width: 991px) {
          text-align: center;
        }
      }

      .line {
        width: pxToEm(80);
        height: pxToEm(1);
        background-color: $turquiose;
        margin-top: pxToEm(20);
      }

      .copy {
        color: #8e8e8e;
        font-size: pxToEm(14);
        margin-top: pxToEm(30);
        max-width: pxToEm(800);
        width: 100%;
        text-align: center;
      }
    }

    .topics {
      margin-top: pxToEm(60);
      display: flex;
      justify-content: space-between;

      @media only screen and (max-width: 991px) {
        flex-direction: column;
        align-items: center;
        margin-bottom: pxToEm(40);
      }

      .topic {
        width: 33%;
        display: flex;
        flex-direction: column;
        align-items: center;

        @media only screen and (max-width: 991px) {
          margin-bottom: pxToEm(40);

          &:last-child {
            margin-bottom: 0;
          }
        }

        @media only screen and (max-width: 599px) {
          width: 100%;
        }

        .icon {
          width: pxToEm(110);
          height: pxToEm(80);
          background-size: contain;
          background-repeat: no-repeat;
          background-position: center;
        }

        > .title {
          color: #707070;
          font-size: pxToEm(32);
          margin-top: pxToEm(20);
        }

        .txt {
          font-family: "OS-Regular";
          color: #7b7b7b;
          font-size: pxToEm(20);
          text-align: center;
          margin-top: pxToEm(10);
        }
      }
    }
  }
}
</style>



