<!-- eslint-disable -->
<template>
  <div class="historia-wrapper">
    <div class="historia movement">
      <div class="copy">
        <div class="title">A nossa história</div>
        <div class="paragraph" v-html="text1"></div>
        <div class="line"></div>
        <div class="paragraph" v-html="text2"></div>
        <div class="quote" v-html="quote"></div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";

export default {
  data() {
    return {
      text1: "",
      text2: "",
      quote: "",
    };
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://www.anacarolinapereira.pt/db-api/getAbout.php")
      .then((resp) => {
        const data = resp.data;
        var result = data.split("||");
        var intro = JSON.parse(result[1]);

        vm.text1 = intro[0].text;
        vm.text2 = intro[0].text2;
        vm.quote = intro[0].quote;
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.historia-wrapper {
  background-color: $white;
  height: pxToEm(510);
  padding: 0 pxToEm(300);
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media only screen and (min-width: 1200px) and (max-width: 1919px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    height: auto;
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    height: auto;
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    height: auto;
    padding: 0 pxToEm(20);
  }

  .historia {
    margin: auto;
    max-width: pxToEm(1400);
    width: 100%;

    @media only screen and (min-width: 768px) and (max-width: 991px) {
      margin: pxToEm(40) 0;
    }

    @media only screen and (min-width: 600px) and (max-width: 767px) {
      margin: pxToEm(20) 0;
    }

    @media only screen and (min-width: 300px) and (max-width: 599px) {
      margin: pxToEm(20) 0;
    }

    .copy {
      display: flex;
      flex-wrap: wrap;
      height: pxToEm(253);
      flex-direction: column;

      @media only screen and (max-width: 991px) {
        flex-wrap: nowrap;
        height: auto;
        align-items: center;
        text-align: center;
      }

      .title {
        font-size: pxToEm(40);
        color: #828282;
        margin-bottom: pxToEm(30);
      }

      .paragraph {
        font-family: "OS-Regular";
        font-size: pxToEm(16);
        color: #828282;
        max-width: pxToEm(600);
        width: 100%;
        height: 40%;

        @media only screen and (max-width: 991px) {
          margin-bottom: pxToEm(20);
        }
      }

      .line {
        height: pxToEm(2);
        width: pxToEm(600);
        background-color: $turquiose;

        @media only screen and (max-width: 991px) {
          margin-bottom: pxToEm(20);
        }
      }

      .quote {
        font-size: pxToEm(20);
        font-family: Georgia, "Times New Roman", Times, serif;
        color: #828282;
        max-width: pxToEm(600);
        width: 100%;
        margin-top: pxToEm(15);

        @media only screen and (max-width: 991px) {
          margin-bottom: pxToEm(20);
        }
      }
    }
  }
}
</style>



