<!--eslint-disable-->
<template>
  <div class="servicos-wrapper">
    <div class="servicos">
      <div class="servico" v-for="s in servicos" :key="s.id">
        <div class="txt-big lateral">{{ s.title }}</div>
        <div class="line lateral"></div>
        <div class="copy movement" v-html="s.subtext"></div>
        <div class="topics movement" v-html="s.text"></div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";

export default {
  data() {
    return {
      servicos: [
        {
          id: "",
          title: "",
          subtext: "",
          text: "",
        },
      ],
    };
  },
  methods: {
    FadeIn() {
      var allMods = $(".movement, .cover");
      var delayedMods = $(".movement-delayed");
      var lateralMods = $(".lateral, .lateralR");
      var svg = $("#svg");

      (function ($) {
        $.fn.visible = function (partial, adjust) {
          if (adjust === undefined) {
            adjust = 0;
          }
          var elem = $(this),
            wind = $(window),
            viewTop = wind.scrollTop(),
            viewBottom = viewTop + wind.height(),
            elem_top = elem.offset().top + adjust,
            elem_bottom = elem_top + elem.height() - adjust,
            compareTop = partial === true ? elem_bottom : elem_top,
            compareBottom = partial === true ? elem_top : elem_bottom;

          return compareBottom <= viewBottom && compareTop >= viewTop;
        };
      })(jQuery);

      var win = $(window);

      var xmlTeste = true;

      allMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          el.addClass("placed");
        }
      });
      svg.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          xmlTeste &&
            el.append(
              "<img src='assets/img/elemento_animated.svg' alt=''></img>"
            );
          xmlTeste = false;
        }
      });
      lateralMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 100)) {
          el.addClass("placed");
        }
      });

      delayedMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 800)) {
          el.addClass("placed");
        }
      });

      win.scroll(function (event) {
        allMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            el.addClass("placed");
          }
        });
        lateralMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 100)) {
            el.addClass("placed");
          }
        });
        delayedMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 800)) {
            el.addClass("placed");
          }
        });
        svg.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            xmlTeste &&
              el.append(
                "<img src='assets/img/elemento_animated.svg' alt=''></img>"
              );
            xmlTeste = false;
          }
        });
      });
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://www.anacarolinapereira.pt/db-api/getAbout.php")
      .then((resp) => {
        const data = resp.data;
        var result = data.split("||");
        var servicos = JSON.parse(result[2]);
        var servicosArray = [];

        map.call(servicos, function (s) {
          servicosArray.push({
            id: s.id,
            title: s.title,
            subtext: s.subtext,
            text: s.text,
          });
        });

        vm.$data.servicos = servicosArray;
      })
      .then(() => {
        this.FadeIn();
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

//paddings and margins
$pd-desktop: 0 130px;
$mg-desktop: 0 40px;

//circle
$circle: 50%;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

.servicos-wrapper {
  height: auto;
  background-color: $white;

  @media only screen and (max-width: 1919px) {
    height: auto;
  }

  .servicos {
    // margin-bottom: pxToEm(70);
    display: flex;
    height: 100%;
    justify-content: space-between;

    @media only screen and (max-width: 991px) {
      flex-direction: column;
    }

    .servico {
      padding: pxToEm(60) pxToEm(80);
      width: 50%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;

      @media only screen and (min-width: 1200px) and (max-width: 1919px) {
        padding: pxToEm(40) pxToEm(60);
      }

      @media only screen and (min-width: 992px) and (max-width: 1199px) {
        padding: pxToEm(40) pxToEm(60);
      }

      @media only screen and (max-width: 991px) {
        width: auto;
        padding: pxToEm(40);
        align-items: center;
      }

      @media only screen and (max-width: 599px) {
        text-align: center;
        align-items: center;
      }

      &:nth-child(2) {
        background-color: $turquiose;

        .txt-big {
          color: $white;
          font-family: "OS-Bold";
        }

        .line {
          background-color: $white;
        }

        .copy {
          color: $white;
          font-family: "OS-Regular";
        }

        .topics {
          color: $white;

          .topic {
            color: $white;
          }
        }
      }

      .txt-big {
        font-size: pxToEm(28);
        color: $dk-gray-3;
        font-family: "OS-Bold";
        margin-bottom: pxToEm(15);

        @media only screen and (max-width: 1919px) {
          margin-bottom: pxToEm(20);
        }
      }

      .line {
        height: pxToEm(1);
        width: pxToEm(80);
        background-color: $turquiose;
        margin-bottom: pxToEm(20);

        @media only screen and (max-width: 1919px) {
          margin-bottom: pxToEm(20);
        }
      }

      .copy {
        color: $dk-gray-3;
        width: 100%;
        font-size: pxToEm(22);
        font-family: "OS-Regular";
        margin-bottom: pxToEm(20);

        @media only screen and (max-width: 1919px) {
          margin-bottom: pxToEm(20);
        }

        @media only screen and (max-width: 991px) {
          text-align: center;
        }
      }

      .topics {
        display: flex;
        flex-direction: column;
        font-family: "OS-Regular";
        font-size: pxToEm(20);

        @media only screen and (max-width: 991px) {
          text-align: center;
        }
      }
    }
  }
}
</style>



