<template>
  <div>
    <banner></banner>
    <historia></historia>
    <servicos></servicos>
    <equipa></equipa>
    <confianca></confianca>
    <marcar-consulta></marcar-consulta>
  </div>
</template>

<script>
/* eslint-disable */
import Banner from "./sobre-nos/Banner";
import Historia from "./sobre-nos/Historia";
import Servicos from "./sobre-nos/Servicos";
import Equipa from "./sobre-nos/Equipa";
import Confianca from "./sobre-nos/Confianca";
import MarcarConsulta from "./sobre-nos/Marcar-consulta";
import FadeIn from "../animations/fade";

export default {
  data() {
    return {
      FadeIn
    };
  },
  components: {
    banner: Banner,
    historia: Historia,
    servicos: Servicos,
    equipa: Equipa,
    confianca: Confianca,
    marcarConsulta: MarcarConsulta
  },
  metaInfo() {
    return {
      title: "Sobre Nós - Ana Carolina Pereira - Clínica de Psicologia",
      meta: [
        {
          name: "description",
          content: "Ana Carolina Pereira - Clínica de Psicologia"
        },
        { name: "robots", content: "index,follow" },
        { property: "og:site_name", content: "Ana Carolina Pereira" },
        { property: "og:type", content: "website" },
        {
          property: "og:title",
          content: "Sobre Nós - Ana Carolina Pereira - Clínica de Psicologia"
        },
        {
          property: "og:description",
          content: "Ana Carolina Pereira - Clínica de Psicologia"
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
</style>



